import React, { Component, ReactNode } from "react";
import Modal from "react-modal";
import { lingoConstants } from "./LingoConstants";

interface IProps {
    modalActive: boolean;
    setModalActive: (value: boolean) => void;
    message: string;
    error: string;
    colorArray: string[];
    word: string;
}

interface IState {
    shareText: string;
}

export default class LingoModal extends Component<IProps, IState> {
    constructor(props: IProps) {
        super(props);

        // Add the modal to the root
        Modal.setAppElement("#root");

        this.state = {
            shareText: "Share",
        };
    }

    share(word: string, colorArray: string[]): void {
        void navigator.clipboard.writeText(getShareable(word, colorArray));
        this.setState({ shareText: "Copied to clipboard!" });
    }

    render(): ReactNode {
        const {
            word,
            modalActive,
            setModalActive,
            message,
            error,
            colorArray,
        } = this.props;

        return (
            // This works as a modal working on the context
            <Modal
                isOpen={modalActive}
                onRequestClose={() => setModalActive(false)}
                contentLabel="Login Container"
                className="login-modal"
                overlayClassName="login-modal-background"
                appElement={document.getElementById("#root")}
            >
                <button
                    className="modal-close"
                    onClick={() => setModalActive(false)}
                />
                <div className="lingo-modal-content">
                    {message ? message + "." : null}
                    {error ? error + "." : null}
                    <br></br>
                    <br></br>
                    {`You have guessed it correctly ${
                        localStorage.getItem("lingoTimesWon") ?? "0"
                    } times!`}
                    <br></br>{" "}
                    {`You did not guess it
                    ${localStorage.getItem("lingoTimesLost") ?? "0"} times!`}
                    <br></br> <br></br>{" "}
                    {`In total you have played Lingo
                    ${
                        localStorage.getItem("lingoTimesPlayed") ?? "0"
                    } times, where on average it took you ${calculateWinRate()} guesses!`}
                    {message ? (
                        <button
                            onClick={() => this.share(word, colorArray)}
                            className="rgb-button white-text login-button lingo-share-button"
                        >
                            <span>{this.state.shareText}</span>
                        </button>
                    ) : null}
                </div>
            </Modal>
        );
    }
}

function calculateWinRate(): number {
    const winsNumberOfGuesses =
        localStorage.getItem("lingoWinGuessAmounts") ?? "";

    // Check if the string is empty
    if (winsNumberOfGuesses === "") {
        return 0;
    }

    let winsSum = 0;
    // Loop over each element in the string
    for (const character of winsNumberOfGuesses) {
        // Check if the current element is a number
        if (isNaN(Number(character))) {
            continue;
        }

        // If it is a number, add it to the sum
        winsSum += Number(character);
    }

    return winsSum / winsNumberOfGuesses.length;
}

function getShareable(word: string, colorArray: string[]): string {
    let sharableLink = `Lingo at Merlijnsevenhuijsen.com/lingo, the word was ${word.toLowerCase()}. \n`;
    let countConsecutiveGreen = 0;
    for (let i = 0; i < colorArray.length && countConsecutiveGreen < 5; i++) {
        switch (colorArray[i]) {
            case "yellow":
                sharableLink += "🟨";
                countConsecutiveGreen = 0;
                break;
            case "green":
                sharableLink += "🟩";
                countConsecutiveGreen++;
                break;
            default:
                sharableLink += "⬜";
                countConsecutiveGreen = 0;
                break;
        }

        if ((i + 1) % lingoConstants.NR_OF_LETTERS == 0) {
            sharableLink += "\n";
        }
    }

    return sharableLink;
}
