import React, { ReactElement } from "react";
import LingoInputRow from "./LingoInputRow";

interface IProps {
    onInput: (input: string) => void;
    inputColorArray: string[];
}

export default function LingoInput({
    onInput,
    inputColorArray,
}: IProps): ReactElement {
    // Constant for each row
    const rowOne = "QWERTYUIOP";
    const rowTwo = "ASDFGHJKL";
    const rowThree = "ZXCVBNM";

    // Function to change
    return (
        <div className="lingo-information">
            <LingoInputRow
                letters={rowOne}
                index={0}
                onInput={onInput}
                letterColors={inputColorArray}
            />
            <LingoInputRow
                letters={rowTwo}
                index={1}
                onInput={onInput}
                letterColors={inputColorArray}
            />
            <LingoInputRow
                letters={rowThree}
                index={2}
                onInput={onInput}
                letterColors={inputColorArray}
            />
        </div>
    );
}

// Function that changes letters into the associated index
export function letterToIndex(letter: string): number {
    return letter.charCodeAt(0) - 65;
}
