import React, { Component, ReactElement } from "react";
import LingoBox from "./LingoBox";
import { lingoConstants, lingoGuessTypes } from "./LingoConstants";
import LingoInput, { letterToIndex } from "./LingoInput";
import LingoModal from "./LingoModal";
import { en, en_words } from "./words/en";
import { nl, nl_words } from "./words/nl";
import { nlSt } from "./words/nl-st";

interface KeyboardEvent {
    key: string;
    code: string;
    preventDefault: () => void;
}

type LingoState = {
    activeRow: number;
    error: string;
    message: string;
    word: string;
    mobileInput: boolean;
    modalActive: boolean;
};

export default class Lingo extends Component<null, LingoState> {
    constructor(props: null) {
        super(props);

        this.state = {
            activeRow: 0,
            error: "",
            message: "",
            word: this.getWord().toUpperCase(),
            mobileInput: true,
            modalActive: false,
        };
    }
    currentGuess = ""; // The guess of the player\
    inputRef: HTMLSpanElement[] = []; // Array of input elements to change color
    mobileButtonRef: HTMLButtonElement[] = []; // Array of buttons to change color
    boxActive = 0; // The current active box (that is selected)
    language: lingoGuessTypes = "dutch"; // The language that is used
    playing = true; // Whether the player is playing the game

    // Array for each color of the boxes within the playing field
    colorArray = Array<string>(
        lingoConstants.NR_OF_LETTERS * lingoConstants.NR_OF_GUESSES
    )
        .fill("var(--navigation-color-septary)")
        .fill(
            "var(--navigation-color-quinary)",
            0,
            lingoConstants.NR_OF_LETTERS
        );
    colorsBoxes: string[] = this.colorArray;

    // Array for the color of the letters
    colorLetterArray = Array<string>(26).fill("lightgray");

    // Listen for keyboard events
    listener = (event: KeyboardEvent): void => {
        event.preventDefault();

        this.handleCharacterInput(event.key);
    };

    // Handle the input of a character
    componentDidMount(): void {
        document.addEventListener("keydown", this.listener);
    }

    // Remove the event listener when the component is unmounted
    componentWillUnmount(): void {
        document.removeEventListener("keydown", this.listener);
    }

    // Start a new game
    startNewGame = (): void => {
        // Reset the colors
        this.colorsBoxes
            .fill("var(--navigation-color-septary)")
            .fill(
                "var(--navigation-color-quinary)",
                0,
                lingoConstants.NR_OF_LETTERS
            );

        // Insert a new word
        this.setState({ word: this.getWord().toUpperCase() });

        // Remove the letters from each input and put colors in
        this.inputRef.forEach((input) => {
            input.children[0].id = "";
            input.children[0].innerHTML = "";
        });

        // Set the active row to the first row, also remove messages
        this.setState({ activeRow: 0, error: "", message: "" });

        // Focus on the first input box
        this.inputRef[0].focus();

        // let the user play the game
        this.playing = true;
    };

    // Handle a change in the guess
    changeGuess(event: React.FormEvent<HTMLInputElement>): void {
        // Get the guess from the event
        const input = event.currentTarget.value;

        // Set the current guess
        this.currentGuess = input;
    }

    // Update the amount of total times played
    updateLocalStorageMetrics(correct: boolean, amount_guesses: number): void {
        // Increase the times played by one
        const timesPlayed = localStorage.getItem("lingoTimesPlayed") ?? "0";
        localStorage.setItem(
            "lingoTimesPlayed",
            (parseInt(timesPlayed) + 1).toString()
        );

        // Update the amount of guesses needed
        const winGuessAmounts =
            localStorage.getItem("lingoWinGuessAmounts") ?? "";
        localStorage.setItem(
            "lingoWinGuessAmounts",
            winGuessAmounts + amount_guesses.toString()
        );

        // If the guess was correct, increase the times won by one
        if (correct) {
            const timesWon = localStorage.getItem("lingoTimesWon") ?? "0";
            localStorage.setItem(
                "lingoTimesWon",
                (parseInt(timesWon) + 1).toString()
            );
        } else {
            const timesLost = localStorage.getItem("lingoTimesLost") ?? "0";
            localStorage.setItem(
                "lingoTimesLost",
                (parseInt(timesLost) + 1).toString()
            );
        }
    }

    // Function to handle input characters
    handleCharacterInput = (inputType: string): void => {
        if (this.playing) {
            switch (inputType) {
                case "Enter":
                case "NumpadEnter":
                    this.handleEnter();
                    break;
                case "Backspace":
                case "Delete":
                    // Remove the last element from the gues
                    this.currentGuess = this.currentGuess.slice(0, -1);

                    // Update the values
                    this.handleCharacterChange(this.currentGuess);
                    break;
                default:
                    if (
                        this.checkValidLetterInput(inputType) &&
                        this.currentGuess.slice(0, -1).length < 4
                    ) {
                        // Update the values
                        this.handleCharacterChange(
                            this.currentGuess + inputType.toUpperCase()
                        );

                        // Handle extra character
                        this.currentGuess =
                            this.currentGuess + inputType.toUpperCase();
                    }
            }
        }
    };

    // Function for setting the playing
    setPlaying = (value: boolean): void => {
        this.playing = value;
    };

    // Remove letter at index i
    removeIndex(input: string, i: number): string {
        return input.slice(0, i) + " " + input.slice(i + 1);
    }

    // Sets the strings of an array
    handleStringsArray = (row: number, color: string): void => {
        for (let i = 0; i < lingoConstants.NR_OF_LETTERS; i++) {
            this.colorArray[row * lingoConstants.NR_OF_LETTERS + i] = color;
        }
    };

    // Remove overlapping characters in two strings, and according to the input color a box
    colorGuesses = (): void => {
        // Make a copy of the current guess which will be edited
        let stringOne: string = this.currentGuess;

        // Make a copy of the current word which will be edited
        let stringTwo = this.state.word;

        // Get the active row
        const startingBoxRow =
            this.state.activeRow * lingoConstants.NR_OF_LETTERS;

        // Create an array of size NR_OF_LETTERS with empty strings
        const stringsArray = new Array<string>(
            lingoConstants.NR_OF_LETTERS
        ).fill("");

        // Color the correct guesses
        for (let i = 0; i < stringOne.length; i++) {
            if (stringOne[i] == stringTwo[i]) {
                // Set the color of the box to the associated color
                stringsArray[i] = "green";

                // Get the index of the input character
                const inputIndex = letterToIndex(stringOne[i]);

                // Set the color of the input character to the associated color
                this.colorLetterArray[inputIndex] = "green";

                // Remove the element from the things to be checked
                stringOne = this.removeIndex(stringOne, i);
                stringTwo = this.removeIndex(stringTwo, i);
            }
        }

        // Color the guesses at the wrong location
        for (let i = 0; i < stringOne.length; i++) {
            if (stringOne[i] != " " && stringTwo.includes(stringOne[i])) {
                // Set the color of the box to yellow
                stringsArray[i] = "yellow";

                // Try to color the input character to yellow if it is not green
                this.colorInputBoxYellow(stringOne[i]);

                // Remove the element from the things to be checked
                const indexRemove = stringTwo.indexOf(stringOne[i]);
                stringOne = this.removeIndex(stringOne, i);
                stringTwo = this.removeIndex(stringTwo, indexRemove);
            }
        }

        // Remove all the letters from the boxes
        for (let i = 0; i < lingoConstants.NR_OF_LETTERS; i++) {
            this.inputRef[startingBoxRow + i].children[0].id =
                "lingo-guess-hidden";
        }

        // For the remainder letters, make the input letters transparent
        for (const char of stringOne) {
            if (char !== " ") {
                // Get the index of the input character
                let inputIndex = letterToIndex(char);

                // If the index is associated with enter, increment it by 1
                if (inputIndex == 13) inputIndex++;

                // The same with backspace
                if (inputIndex == 8) inputIndex++;

                // Set the color of the input character to the associated color
                this.colorLetterArray[inputIndex] =
                    "var(--navigation-color-tertiary)";
            }
        }

        // Wait one second
        // Color all the boxes starting from the active row
        for (let i = 0; i < stringsArray.length; i++) {
            // // Wait one 0.1 second
            setTimeout(() => {
                // Color the background box
                if (stringsArray[i] != "") {
                    this.colorArray[startingBoxRow + i] = stringsArray[i];
                    this.inputRef[startingBoxRow + i].setAttribute(
                        "style",
                        `background-color: ${stringsArray[i]}`
                    );
                }

                this.inputRef[startingBoxRow + i].children[0].id =
                    "lingo-letters-show";
            }, 100 * (i + 2));
        }
    };

    // Color the input boxes
    colorInputBoxYellow = (guessLetter: string): void => {
        // Get the index of the guessed letter
        const inputIndex = letterToIndex(guessLetter);

        if (this.colorLetterArray[inputIndex] != "green") {
            this.colorLetterArray[inputIndex] = "yellow";
        }
    };

    // Function that fetches a random word from the word lists
    getWord = (): string => {
        if (this.language == "dutch") {
            return nl_words[Math.floor(Math.random() * nl_words.length)];
        } else if (this.language == "english") {
            return en_words[Math.floor(Math.random() * en_words.length)];
        } else if (this.language == "straattaal") {
            return nlSt[Math.floor(Math.random() * nlSt.length)];
        }
    };

    // Validate the input of the user
    validateInput = (input: string): boolean => {
        if (
            input.length == lingoConstants.NR_OF_LETTERS &&
            (this.language == "dutch" || this.language == "straattaal")
        ) {
            return (
                nl.includes(input.toLowerCase()) ||
                nlSt.includes(input.toLowerCase())
            );
        } else if (
            input.length == lingoConstants.NR_OF_LETTERS &&
            this.language == "english"
        ) {
            return en.includes(input.toLowerCase());
        } else {
            return input.length == lingoConstants.NR_OF_LETTERS;
        }
    };

    // Make the colors temporary red
    giveErrorToUser = (): void => {
        // Get the active row
        const startingBoxRow =
            this.state.activeRow * lingoConstants.NR_OF_LETTERS;

        for (let i = 0; i < lingoConstants.NR_OF_LETTERS; i++) {
            this.inputRef[startingBoxRow + i].setAttribute(
                "style",
                `background-color: LightCoral`
            );
        }

        // Wait one second and then make the colors back to normal
        setTimeout(() => {
            for (let i = 0; i < lingoConstants.NR_OF_LETTERS; i++) {
                this.inputRef[startingBoxRow + i].setAttribute(
                    "style",
                    `background-color: var(--navigation-color-quinary)`
                );
            }
        }, 200);
    };

    // Handle all the colors of a guess
    handleColors = (): void => {
        // Color this row to the background color
        this.handleStringsArray(
            this.state.activeRow,
            "var(--navigation-color-septary)"
        );

        // Color the next row
        this.handleStringsArray(
            this.state.activeRow + 1,
            "var(--navigation-color-quinary)"
        );

        // check the guesses and provide feedback to the user
        this.colorGuesses();
    };

    // Handle character change
    handleCharacterChange = (input: string): void => {
        for (let i = 0; i < lingoConstants.NR_OF_LETTERS; i++) {
            const element = this.inputRef[
                this.state.activeRow * lingoConstants.NR_OF_LETTERS + i
            ];
            if (input[i]) {
                element.children[0].innerHTML = input[i];
                element.children[0].id = "lingo-letters-show";
            } else {
                element.children[0].innerHTML = "";
            }
        }
    };

    // Checks if an input consists only of letters
    checkValidLetterInput = (input: string): boolean => {
        return /[a-zA-Z_ ]/.test(input) && input.length == 1;
    };

    // Correct guess function
    correctGuess(): void {
        // Set the colors to the row to green
        this.handleStringsArray(this.state.activeRow, "green");

        // Update the metrics
        this.updateLocalStorageMetrics(true, this.state.activeRow);

        // Increase the current row by 1 such that you cannot change it anymore
        // enable the modal
        // Message the user that he has won
        this.setState({
            activeRow: this.state.activeRow + 1,
            modalActive: true,
            message: `You win! The word was ${this.state.word.toLowerCase()}`,
        });

        // Stop the user from playing
        this.setPlaying(false);
    }

    // Wrong guess function
    wrongGuess = (): void => {
        // Calculate the next row
        const nextRow = this.state.activeRow + 1;

        // Check if the game is over
        if (nextRow >= lingoConstants.NR_OF_GUESSES) {
            // Update the metrics
            this.updateLocalStorageMetrics(false, this.state.activeRow);

            // Tell the user that he has lost
            this.setState({
                error: `You lost! The word was ${this.state.word.toLowerCase()}`,
                modalActive: true,
            });

            // Stop the user from playing
            this.setPlaying(false);
        } else {
            // Display the colors to the user
            this.handleColors();
        }

        // Clear the current guess
        this.currentGuess = "";

        this.setState({ activeRow: nextRow });
    };

    // Handles an enter press
    handleEnter = (): void => {
        // Validate input
        if (this.validateInput(this.currentGuess)) {
            // If there are errors then remove them
            if (this.state.error) this.setState({ error: "" });

            // Check if the word is correct
            this.currentGuess.toUpperCase() == this.state.word
                ? this.correctGuess()
                : this.wrongGuess();
        } else {
            this.giveErrorToUser();
        }
    };

    // Function that enables the modal
    setModal = (value: boolean): void => {
        this.setState({ modalActive: value });
    };

    render(): ReactElement {
        return (
            <div className="container" id="Home">
                <LingoModal
                    modalActive={this.state.modalActive}
                    setModalActive={this.setModal}
                    message={this.state.message}
                    error={this.state.error}
                    colorArray={this.colorArray}
                    word={this.state.word}
                />
                <div className="lingo-information">
                    <h1 style={{ fontSize: "80px" }}>Wordle</h1>
                </div>
                <div className="lingo-game-border">
                    <LingoBox
                        colorBoxes={this.colorsBoxes}
                        inputRef={this.inputRef}
                    />
                    <div className="lingo-row">
                        <input
                            maxLength={5}
                            type="text"
                            onChange={(e) => {
                                this.changeGuess(e);
                            }}
                        />
                    </div>
                </div>
                {this.state.mobileInput ? (
                    <LingoInput
                        onInput={this.handleCharacterInput}
                        inputColorArray={this.colorLetterArray}
                    />
                ) : null}
                <div className="lingo-information">
                    <h2>Controls</h2>
                    <button
                        onClick={() => this.startNewGame()}
                        className="rgb-button login-button transparent-button white-text"
                    >
                        <span>New Game</span>
                    </button>
                    <h3>Enable mobile input</h3>

                    <input
                        type="checkbox"
                        id="mobileInput"
                        checked={this.state.mobileInput}
                        onChange={(e) => {
                            this.setState({ mobileInput: e.target.checked });
                        }}
                    />

                    <div className="lingo-fieldset">
                        <fieldset>
                            <input
                                type="radio"
                                id="dutch"
                                name="language-wordle"
                                onChange={(e) =>
                                    (this.language = e.target
                                        .id as lingoGuessTypes)
                                }
                                checked={this.language == "dutch"}
                            />
                            <label htmlFor="dutch">Dutch</label>
                            <br />
                            <input
                                type="radio"
                                id="english"
                                name="language-wordle"
                                onChange={(e) =>
                                    (this.language = e.target
                                        .id as lingoGuessTypes)
                                }
                                checked={this.language == "english"}
                            />
                            <label htmlFor="english">English</label>
                            <br />
                            <input
                                type="radio"
                                id="straattaal"
                                name="language-wordle"
                                onChange={(e) =>
                                    (this.language = e.target
                                        .id as lingoGuessTypes)
                                }
                                checked={this.language == "straattaal"}
                            />
                            <label htmlFor="straattaal">
                                Dutch (Straattaal)
                            </label>
                            <br />
                        </fieldset>
                    </div>
                </div>
                <div className="lingo-information">
                    <h2>How to play</h2>
                    <p>
                        This game is about trying to guess the word in a few
                        guesses. Only valid words can be inserted into a row.
                        The game starts from the top and goes row by row down to
                        the bottom. Each word that one can guess consists of 5
                        letters. You can insert a guess by pressing the enter
                        key on the keyboard, it will then move on to the next
                        row and indicate what letters were wrong. No special
                        indication means that the letter is not in the word, a
                        yellow indication means that the letter is in the word
                        but at a different location, and green shows that the
                        letter is indeed in the word and at the correct
                        position.
                    </p>
                </div>
            </div>
        );
    }
}
