import React, { ReactElement } from "react";
import { lingoConstants } from "./LingoConstants";

interface IProps {
    colorBoxes: string[];
    inputRef: HTMLSpanElement[];
}

export default function LingoBox({
    colorBoxes,
    inputRef,
}: IProps): ReactElement {
    return (
        <div
            className="lingo-box"
            style={{
                gridTemplateColumns: "1fr ".repeat(
                    lingoConstants.NR_OF_LETTERS
                ),
            }}
        >
            {Array.from(
                {
                    length:
                        lingoConstants.NR_OF_GUESSES *
                        lingoConstants.NR_OF_LETTERS,
                },
                (_, index) => (
                    <span
                        key={index}
                        style={{ background: colorBoxes[index] }}
                        ref={(divElement) => (inputRef[index] = divElement)}
                    >
                        <div></div>
                    </span>
                )
            )}
        </div>
    );
}
