import React, { ReactElement } from "react";
import { letterToIndex } from "./LingoInput";
import LingoInputContainer from "./LingoInputContainer";

interface IProps {
    letters: string;
    index: number;
    onInput: (input: string) => void;
    letterColors: string[];
}

export default function LingoInputRow({
    letters,
    index,
    onInput,
    letterColors,
}: IProps): ReactElement {
    // Create a button for a specific letter
    function createButton(letter: string, isLetter: boolean): ReactElement {
        return (
            <LingoInputContainer
                onInput={onInput}
                letter={letter}
                key={letter}
                color={isLetter ? letterColors[letterToIndex(letter)] : "white"}
            />
        );
    }

    // Create a row of buttons
    function createRow(row: string, row_count: number): ReactElement {
        return (
            <div className="lingo-mobile-row">
                {row
                    .split("")
                    .map((letter: string, _) => createButton(letter, true))}
                {row_count === 0 ? createButton("Backspace", false) : null}
                {row_count === 1 ? createButton("Enter", false) : null}
            </div>
        );
    }

    return (
        <div className="lingo-mobile-input-row">
            {createRow(letters, index)}
        </div>
    );
}
