import React, { CSSProperties, ReactElement } from "react";

interface IProps {
    onInput: (input: string) => void;
    letter: string;
    color: string;
}

export default function LingoInputContainer({
    onInput,
    letter,
    color,
}: IProps): ReactElement {
    // Custom style for the button
    let customStyle: CSSProperties = {
        backgroundColor: color,
    };

    // If the length of the letter is greater than 1, then add a larger width
    if (letter.length > 1) {
        customStyle = {
            ...customStyle,
            width: "100px",
        };
    }

    return (
        <button
            className="lingo-mobile-input"
            style={customStyle}
            onClick={() => onInput(letter)}
        >
            {letter}
        </button>
    );
}
